import React from 'react'

function Faqs() {
    return (
        <div>
            <h6 className=" fw-700 text-dark">What is a car loan?</h6>
            <p className="fs-14 mb-xl-20">A car loan is a loan provided by a bank or NBFC to purchase a car basis various factors such as your repayment capacity, credit score, age etc.</p>
            
            <h6 className=" fw-700 text-dark">Can a car loan be availed to purchase a used car?</h6>
            <p className="fs-14 mb-xl-20">Yes, car loan can be availed for pre-used cars as well. However, the interest rate for a loan for used to car differs from that of a purchase of a new car. The loan would only cover the price of the car, other costs such as transfer of registration, etc. will have to borne by you.</p>
            
            <h6 className=" fw-700 text-dark">Should there be collateral given for a car loan?</h6>
            <p className="fs-14 mb-xl-20">A car loan is a secured loan where the car bought itself acts as collateral. There is no additional collateral that needs to be given requirement for a car loan. However, you do have to get the RC (registration certificate) of the car endorsed with the bank. This endorsement is cancelled after repayment of the loan is completed.</p>
            
            <h6 className=" fw-700 text-dark">What is the maximum loan amount one is eligible for?</h6>
            <p className="fs-14 mb-xl-20">The maximum loan amount that one can avail will vary from one bank to the other. Usually, it is between 80%–90% of the car’s on-road price. There are a few banks that even provide 100% of the car’s ex-showroom price. The percentage of funding also depends on the price, type of car (standard/ premium) and whether it is a new or pre-owned car.</p>

            <h6 className=" fw-700 text-dark">What are the documents needed to be submitted when applying for a car loan?</h6>
            <p className="fs-14 mb-xl-20">The documents needed for a car loan are income proofs (last three pay slips/last acknowledged ITR), address and identity proofs along with PAN card. Other documentation requirements, if any, tend to differ from one lender to another.</p>

            <h6 className=" fw-700 text-dark">What is the typical tenure of a car loan?</h6>
            <p className="fs-14 mb-xl-20">It usually ranges from 1 year to 5 years. If your loan tenure is shorter, then the EMI you pay is higher is. There are a few banks providing car loans for longer durations of up to 7 years.</p>

            <h6 className=" fw-700 text-dark">Is there a minimum salary requirement to apply for a car loan?</h6>
            <p className="fs-14 mb-xl-20">There is no specification of minimum salary requirement from most banks, however, chances of loan application getting rejected in case your salary does not exceed a predetermined threshold level as required by the internal requirement of the bank is higher. In cases of salary requirement not matching you could apply the loan with a co-applicant.</p>

            <h6 className=" fw-700 text-dark">Are there chances that the car loan application can be rejected?</h6>
            <p className="fs-14 mb-xl-20">In cases of a bad credit score, past cases of defaulting on repayments, earlier loan applications rejected multiple times, etc. In addition, bank’s eligibility criteria such as minimum income level, age, previous relationship with the bank, etc. needs to be met to get your loan approved.</p>

            <h6 className=" fw-700 text-dark">Are there any tax benefits of a car loan?</h6>
            <p className="fs-14 mb-xl-20">No. Unlike a home loan, a car loan has no tax savings benefits what so ever. </p>

            <h6 className=" fw-700 text-dark">Fixed rate vs floating rate, which is applicable for car loan?</h6>
            <p className="fs-14 mb-xl-20">In fixed interest rate on car loan, the interest remains the same for a period of time. In such a case, car loans have a specific EMI payout requirement that does not change due to market conditions. In floating rate car loan, the applicable interest rate of the car loan varies periodically as per the decision of the bank. Thus the payable EMI on a floating rate loan may change from time to time.</p>

        </div>
    )
}

export default Faqs
